#nav {
  width: 100%;
  background-color: black;
  color: white;
  padding: 20px;
  text-align: right;
  height: 60px;
  z-index: 10;

  ul,
  li {
    list-style-type: none;
  }
  #nav-list {
    width: 60%;
    display: inline-flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: auto;
    text-align: center;
  }

  #nav-list li {
    align-items: center;
    text-align: center;
    margin: auto;
    justify-content: center;
    cursor: default;
  }

  .dropdown-content {
    padding: 20px 0 10px 0;
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: black;
    display: none;
    z-index: 20;
  }

  .dropdown-content a {
    padding: 15px 60px 10px 15px;
    display: block;
    border-top: solid 1px rgba(139, 139, 139, 0.404);
    text-align: left;
  }

  a,
  a:visited,
  a:hover {
    cursor: pointer;
    color: white;
    text-decoration: none;
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }
}
